.card-body{
  height: 6rem;
    width: 20rem;
    background-color: lightgrey;
    border-radius: 1rem;
    display: flex;
    justify-content: center;
    box-shadow: 6px 5px 5px grey;
    align-items: center;
}

.card-body span{
  width: 100%;
  font-size: 5rem;
  display: flex;
  justify-content: center;
  position: relative;
  font-weight: 600;
}


.card-body span.num {
  font-size: 4.5rem;
  border-right: 2px solid rgba(0,0,0,0.2);
  border-left: 2px solid rgba(1,1,1,0.2);

}

.card{
  display: flex;
  justify-content: center;
  height: 21rem;
  align-items: flex-end;
}

.card1{
  display: flex;
    height: 20rem;
    justify-content: center;
    align-items: center;
}

.num, .button1, .button2{
  font-family: 'Raleway', sans-serif;
}

.number{
  font-size: 80px;
    font-weight: 700;
    font-family: 'Raleway', sans-serif;
}

.p1 .p2{
  font-family: 'Raleway', sans-serif;
}
body{
    margin: 0px;
}
.nav-link, .dian, .count{
    font-family: 'Raleway', sans-serif;
    color:white;
    text-decoration: none;
}

.container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: black;
}

.nav-item{
    padding: 0 37px;
}

ul{
    display:flex;
}

.dian{
    padding: 24px;
    font-size: 21px;
    font-weight: bolder;
    font-family: 'Raleway', sans-serif;
}

.count{
    padding: 0 36px
}

.nav-link{
    font-family: 'Raleway', sans-serif;
   

}

.dians{
    font-weight: 600;
    color: white;
    background-color:rgb(73, 14, 62);
    border-color: white;
    padding: 9px;
    /* height: 40px; */
    /* width: 50px; */
    border-radius: 82px;
    text-decoration: none;
}

    

.dianapp{
    padding: 0 24px;
    position: relative;
    z-index: 0;
    margin-right: 60px;
}
   


.ccc{
    position: absolute;
    z-index: -2;
    top: -35px;
    padding: 21px; 
    right: -20px;
    /* height: 60px;
    width: 60px; */
   

}

button{
    padding:3px;
    position:absolute;
    left: -29px;
    top: -7px;
    border-radius: 6px;
    color:white;
    background-color: teal;
    font-family: 'Raleway', sans-serif;
}

@media only screen and (min-width: 320px) and (max-width: 960px) {
    .container {
      width:222%;
    }
    
  }

.p1, .p2{
    font-family: 'Raleway', sans-serif;
}
